import Vue from 'vue';
import Vuex from 'vuex';
import Auth from 'firebase';
import { ServerEventUser } from '@servicestack/client';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        user: null,
    },
    actions: {
        logUserIn({ commit }) {
            document.write('login');
        },
        signUserUp({ commit }, payload) {
            const newUser = {
                id: 'test',
                registeredEvents: ['1', '2'],
            };
            store.commit('setUser', newUser);
            
            // Auth.auth().createUserWithEmailAndPassword(payload.email, payload.password)
            //     .then(
            //     (response) => {
            //         const newUser = {
            //             id: response.user == null ? '' : response.user.uid,
            //             registeredEvents: [],
            //         };
            //         commit('setUser', newUser);
            //     })
            //     .catch(
            //         (error) => {
            //             console.log(error);
            //         },
            //     );
        },
    },
    mutations: {
        setUser(state, payload) {
            state.user = payload;
        },
    },
    getters: {
        getUser(state) {
            return state.user;
        },
    },
    modules: {

    },
});

export default store;
