import Vue from 'vue';
import Router from 'vue-router';

import Home from './home/Home.vue';
import View1 from './view1/View1.vue';
import View2 from './view2/View2.vue';
import Login from './login/Login.vue';
import Signup from './login/Signup.vue';

Vue.use(Router);

